html {
	font-size: 10px;
}

* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
	background-repeat: no-repeat;
	background-position: center center;
	background-attachment: fixed;
	background-size: cover;
	width: 100%;
	height: 100%;
	color: black;
	font-family: 'Montserrat', sans-serif;
}

h1 {
	color: white;
}

img {
	border-radius: 5px;
	box-shadow: 0 8px 10px -6px black;
}

p {
	font-size: 1.6rem;
}

small {
	font-size: 1.2rem;
}

.container {
	margin: 0 auto;
	max-width: 1000px;
	padding: 40px;
}

.title {
	font-size: 4.4rem;
	text-align: center;
}

h3 {
	font-size: 1.5rem;
	text-align: center;
}

.form {
	display: flex;
	justify-content: flex-start;
}

.label {
	font-size: 1.2rem;
	margin-bottom: 0.2rem;
	text-transform: uppercase;
	margin-right: 1rem;
}

.input {
	font-size: 1.6rem;
	padding: 0.5rem 2rem;
	line-height: 2.8rem;
	border-radius: 20px;
	border: 1px solid #ddd;
	margin-bottom: 1rem;
	margin-right: 1rem;
	width: 300px;
	outline: none;
}

.button {
	background-color: rgba(0, 0, 0, 0.75);
	color: white;
	padding: 1rem 2rem;
	border: 1px solid rgba(0, 0, 0, 0.75);
	border-radius: 20px;
	font-size: 1.4rem;
	cursor: pointer;
	transition: background-color 250ms;
	outline: none;
}

.button:hover {
	background-color: rgba(0, 0, 0, 0.85);
}

@media only screen and (max-width: 600px) {
	.form {
		display: flex;
		align-items: center;
	}

	.label {
		margin-bottom: 10px;
		width: 115px;
	}

	.button {
		margin-bottom: 10px;
		margin-right: 20px;
	}
}

@media (min-width: 600px) and (max-width: 767px) {
	.form {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.label {
		display: flex;
		margin-bottom: 10px;
		width: 0 auto;
	}

	.input {
		width: 0 auto;
	}

	.button {
		margin-bottom: 10px;
	}
}

@media only screen and (min-width: 768px) {
	.form {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.card {
		display: flex;
		flex-direction: row;
	}

	.card--content {
		margin-left: 2rem;
	}

	.label {
		margin-bottom: 10px;
	}

	.input {
		width: 0 auto;
	}

	.button {
		margin-bottom: 10px;
	}
}

@media only screen and (min-width: 1200px) {
	.card {
		display: flex;
	}
}

/* Movie Card Styling */
.card-list {
	margin-top: 4rem;
}

.card {
	padding: 2rem 4rem;
	border-radius: 10px;
	box-shadow: 5px 4px 5px rgba(0, 0, 0, 0.25);
	margin-bottom: 2rem;
	background-color: white;
}

.card--title {
	margin-bottom: 0.5rem;
	font-size: 3.2rem;
}

.card--image {
	margin: 0 auto;
	display: block;
}
